import _ from 'lodash'
import URI from 'urijs'
import JSON from 'JSON'

import globalConfiguration from '../../configuration'

// Parse url
const uri = new URI()
const query = uri.search(true)

// Runtime configuration passes
// as query string param.
// JSON is expected.
let runtime = ''
if (_.has(query, 'configuration')) {
  try {
    runtime = JSON.parse(query.configuration)
  } catch (e) {

  }
}

// Test configuration
if (process && process.env && process.env.NODE_ENV === 'testing') {
  runtime = {
    api: {
      backendURL: '',
      timeout: 1000
    }
  }
}

// Configuration object
export default _.defaults(runtime, {
  api: {
    token: {
      query: 'demo'
    }
  },
  links: {
    dashboardURL: globalConfiguration.frontend.url + '/dashboard.html',
    authenticationURL: globalConfiguration.frontend.url + '/authentication.html',
    slackURL: 'http://goo.gl/3nrh3r',
    documentationURL: globalConfiguration.frontend.url + '/documentation/index.html',
    tutorialReadURL: 'https://medium.com/@jonathan.godwin/91bc061e5619',
    planFreeURL: globalConfiguration.frontend.url + '/authentication.html',
    planBasicURL: globalConfiguration.frontend.url + '/authentication.html',
    planProURL: globalConfiguration.frontend.url + '/authentication.html',
    planEnterpriseURL: 'mailto:contact@bloomsbury.ai',
    addSlackURL: 'slack.html',
    chromeURL: 'https://chrome.google.com/webstore/detail/cape-super-powered-ctrl-f/ndpjhckhhfpcloemggoacecenhajokml?hl=en-GB&gl=GB'
  }
})



